import React from 'react'
import { getName } from '../../utils'
import { SongList, SongItem } from './style'
import {
  changePlayList,
  changeCurrentIndex,
  changeSequencePlayList,
} from '../../application/Player/store/actionCreators'
import { connect } from 'react-redux'

const SongsList = React.forwardRef((props, refs) => {
  const { collectCount, showCollect, songs } = props
  const {
    changePlayListDispatch,
    changeCurrentIndexDispatch,
    changeSequencePlayListDispatch,
  } = props

  // 
  const { musicAnimation } = props

  const totalCount = songs.length
  const selectItem = (e, index) => {
    changePlayListDispatch(songs)
    changeSequencePlayListDispatch(songs)
    changeCurrentIndexDispatch(index)
    musicAnimation(e.nativeEvent.clientX, e.nativeEvent.clientY)
  }

  const songList = (list) => {
    const res = []
    for (let i = 0; i < list.length; i++) {
      const item = list[i]
      res.push(
        <li key={item.id} onClick={(e) => selectItem(e, i)}>
          <span className="index">{i + 1}</span>
          <div className="info">
            <span>{item.name}</span>
            <span>
              {item.ar ? getName(item.ar) : getName(item.artist)} -{' '}
              {item.al ? item.al.name : item.album.name}
            </span>
          </div>
        </li>,
      )
    }
    return res
  }

  const collect = (count) => {
    return (
      <div className="add_list">
        <i className="iconfont">&#xe62d;</i>
        <span> ({Math.floor(count / 1000) / 10} )</span>
      </div>
    )
  }

  return (
    <SongList ref={refs} showBackground={props.showBackground}>
      <div className="first_line">
        <div className="play_all" onClick={(e) => selectItem(e, 0)}>
          <i className="iconfont">&#xe6e3;</i>
          <span>
            <span className="sum">( {totalCount} )</span>
          </span>
        </div>
        {showCollect ? collect(collectCount) : null}
      </div>
      <SongItem>{songList(songs)}</SongItem>
    </SongList>
  )
})

const mapDispatchToProps = (dispatch) => {
  return {
    changePlayListDispatch(data) {
      dispatch(changePlayList(data))
    },
    changeCurrentIndexDispatch(data) {
      dispatch(changeCurrentIndex(data))
    },
    changeSequencePlayListDispatch(data) {
      dispatch(changeSequencePlayList(data))
    },
  }
}

export default connect(null, mapDispatchToProps)(React.memo(SongsList))
